.sameSize {
  overflow: hidden;
  padding: 2em;
  margin-top: 2em;
  max-height: 350px;
  min-height: 250px;
  object-fit: cover;
  width: auto;
  cursor: pointer;
  opacity: 1;
}

.centerSlide img:hover {
  opacity: 0.4;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  filter: grayscale(100%);
  -webkit-filter: blur(4px);
  filter: blur(4px);
}

.centered {
  padding-bottom: 5em;
  text-align: center;
}

.slick-next:before {
  position: absolute;
  color: transparent;
  right: 1em;
  width: 20px;
  height: 35px;
  background: url(https://firebasestorage.googleapis.com/v0/b/portfolio-53c6c.appspot.com/o/right.png?alt=media&token=f3f1a520-7e72-4387-915f-b622fed385e7);
}

.slick-prev:before {
  position: absolute;
  color: transparent;
  left: 1em;
  width: 20px;
  height: 35px;
  background: url(https://firebasestorage.googleapis.com/v0/b/portfolio-53c6c.appspot.com/o/left.png?alt=media&token=be1975b6-3759-4d20-960c-d9789671c4d7);
}

body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.5;
  color: whitesmoke;
  overflow-x: hidden;
  background-color: #14131f;
}
.tiny {
  height: 25px;
  margin-top: -1em;
  margin-left: 2em;
}
.tinyFirst {
  height: 25px;
  margin-top: -1em;
  margin-left: 0em;
}
.container {
  max-width: 960px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: auto;
  text-align: center;
}
.flexContainer {
  margin-top: 5em;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.fullscreen-video-wrap {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -1000;
  overflow: hidden;
}

.fullscreen-video-wrap video {
  min-height: 100%;
  min-width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.storyboard {
  /* Kanske behövs max här för att få det att flyta på bra på mindre skärmar */
  max-width: 95%;
}
